<style>
    /**/
    *{
        padding: 0;
        margin: 0;
    }

    /*div*/
    #workspace-text{
        width: 500px;
        height: 500px;
        padding: 10px;
        border: 2px lightgray solid;
        position: absolute;
    }

    /*li3*/
    li{
        list-style: none;
        float: right;
        margin: 5px;
        cursor: pointer;
        display: inline-block;
        height: 30px;
        width: 90px;
        text-align: center;
        background-color: lightgray;
        line-height: 30px;
    }

    /*3li*/
    #workspace-text-head li.li-now{
        background-color: darkgray;
        color: black;
        font-weight: bold;
    }

    /**/
    #workspace-text-body {
        clear: both;
        padding-left: 50px;;
    }

    /*div33div*/
    #workspace-text-body > div {
        display: none;
    }

    /*3*/
    #workspace-text-body > div.div-now {
        display: block;
    }

    /**/
    #btn-edit-record{
        position: absolute;
        left: 50px;
        bottom: 10px;
    }

    /**/
    #btn-edit{
        position: absolute;
        right: 50px;
        bottom: 10px;
    }

    /**/
    #btn-cancelEdit{
        position: absolute;
        right: 100px;
        bottom: 50px;
    }

    /**/
    #btn-confirmEdit{
        position: absolute;
        right: 50px;
        bottom: 50px;
    }

    /**/
    #btn-addComment{
        position: absolute;
        right: 50px;
        bottom: 50px;
    }

    /**/
    #btn-mark-noumenon{
        position: absolute;
        right: 50px;
        bottom: 50px;
    }

    /**/
    #select-markType {
        position: absolute;
        right: 20px;
        top: 80px;
    }

    /**/
    .body-text{
        margin-top: 40px;
    }

    /*&*/
    textarea{
        position: relative;
        top: 30px;
        width: 320px;
    }

    /**/
    #textarea-editInfo{
        position: relative;
        top: 0px;
    }

    /**/
    #textarea-addComment{
        width: 500px;
        height: 200px;
        position: static;
    }

    /**/
    .comment{
        cursor: pointer;
        text-decoration: underline;
    }

    /**/
    .mark{
        cursor: pointer;
        font-weight: bold;
    }
</style>


