

<!---->
<template >
    <div id="user_box">

        <!---->
        <div id="table_container">

            <!---->
            <div id="user_text">
                <img class="image" src="src/assets/img/.png" height="25" width="25"/>
                <span class="span_user"></span>
            </div>

            <!---->
            <div id="create_user">
                <input type="button" class="btn_general" value=""  @click="open_dialog()">
            </div>

            <!---->
            <div id="table_box">
                    <table id="table_all">
                        <!---->
                        <tr>
                            <th class="table_border"></th>
                            <th class="table_border"></th>
                            <th class="table_border"></th>
                            <th class="table_border"></th>
                        </tr>

                        <!---->
                        <tr v-for="(item,index) in userData"> <!--v-for-->
                            <td class="table_border" >
                                {{item.user_name}}
                            </td>
                            <td class="table_border">
                                {{item.account}}
                            </td>
                            <td class="table_border">
                                <input type="password" class="input_pwd" value="********" readonly>

                            </td>
                            <td class="table_border">
                                <input type="button"  class="btn_general" value="" @click="open_chaDialog(index)"><!--v-on click index-->
                                <input type="button" class="btn_general" value="" @click="deleteUsers(index)">
                            </td>
                        </tr>
                    </table>
            </div>
        </div>

        <!---->
        <div class="dialogs" v-bind:show.sync="show_create">
            <div class="dialog" v-bind:class="{ 'dialog-active': show_create }">
                <div class="dialog-content">

                    <header class="dialog-header">
                        <span class="dialog-title"></span>
                        <span class="close rotate iconfont icon-close" @click="close_dialog()"></span><!---->
                    </header>

                    <div class="dialog-body" >
                        <p>
                            <span class="span_motal_1"></span>
                            <span class="span_motal_2">:</span>
                            <input type="text"  id="text1" class="input_motal" placeholder="2-10" onfocus="this.placeholder=''" @blur="checkIn('text1','2-10','^[\u4e00-\u9fa5a-zA-Z0-9]{2,10}$',0)"  v-model="user_name" v-bind:class="{warnBorder:this.isActive[0]}">
                        </p>

                        <p>
                            <span class="span_motal_1"></span>
                            <span class="span_motal_2">:</span>
                            <input type="text"  id="text2" class="input_motal"  placeholder="8-10" onfocus="this.placeholder=''" @blur="checkIn('text2','8-10','^[a-zA-Z0-9]{8,10}$',1)" v-model="account" v-bind:class="{warnBorder:this.isActive[1]}">
                        </p>

                        <p>
                            <span class="span_motal_1"></span>
                            <span class="span_motal_2">:</span>
                            <input type="password"  id="text3" class="input_motal" placeholder="6-16"  onfocus="this.placeholder=''" @blur="checkIn('text3','6-16','^[a-zA-Z0-9_]{6,16}$',2)" v-model="pwd" v-bind:class="{warnBorder:this.isActive[2]}">
                        </p>

                        <p>
                            <span class="span_motal_1"></span>
                            <span class="span_motal_2">:</span>
                            <input type="password" id="text4" class="input_motal" placeholder="" onfocus="this.placeholder=''" @blur="check_pwd('text3','text4')" v-model="confirm_pwd" v-bind:class="{warnBorder:this.isActive[3]}">
                        </p>
                    </div>
                    <footer class="dialog-footer" >
                        <!---->
                        <p  class="warn_tip" v-model="tip">{{tip}}</p>
                        <!---->
                        <div>
                            <button class="btn_general" style="margin-right: 10%" @click="close_dialog()"></button>
                            <button class="btn_general "  @click="add_msg()"></button>
                        </div>
                    </footer>
                </div>
            </div>
            <div class="dialog-overlay"></div>
        </div>


        <!---->
        <div class="dialogs" v-bind:show.sync="show_change">
            <div class="dialog" v-bind:class="{ 'dialog-active': show_change }">
                <div class="dialog-content">

                    <header class="dialog-header">
                        <span class="dialog-title"></span>
                        <span class="close rotate iconfont icon-close" @click="close_chaDialog()"></span><!---->
                    </header>

                    <div class="dialog-body">
                        <p>
                            <span class="span_motal_1"></span>
                            <span class="span_motal_2">:</span>
                            <input type="text" name="user_name" id="chan1" class="input_motal" v-model="back_account" readonly style="color: dimgrey;font-size: 15px;font-weight: 400;border-color: transparent; ">
                        </p>

                        <p >
                            <span class="span_motal_1"></span>
                            <span class="span_motal_2">:</span>
                            <input type="text"  id="chan2" class="input_motal" v-model="back_username" @blur="checkIn('chan2','2-10','^[\u4e00-\u9fa5a-zA-Z0-9]{2,10}$',0)" v-bind:class="{warnBorder:this.isActive[0]}">
                        </p >

                        <p >
                            <span class="span_motal_1"></span>
                            <span class="span_motal_2">:</span>
                            <input type="password"  id="chan3" class="input_motal" placeholder="6-16"  onfocus="this.placeholder=''" @blur="checkIn('chan3','6-16','^[a-zA-Z0-9_]{6,16}$',2)" v-model="pwd"  v-bind:class="{warnBorder:this.isActive[2]}" >
                        </p>

                        <p>
                            <span class="span_motal_1"></span>
                            <span class="span_motal_2">:</span>
                            <input type="password" id="chan4" class="input_motal" placeholder="" onfocus="this.placeholder=''" @blur="check_pwd('chan3','chan4')" v-model="confirm_pwd" v-bind:class="{warnBorder:this.isActive[3]}">
                        </p>
                    </div>

                    <footer class="dialog-footer" slot="footer">
                        <!---->
                        <span class="warn_tip" v-model="tip">{{tip}}</span>
                        <div>
                            <button class="btn_general" style="margin-right: 10%" @click="close_chaDialog()"></button>
                            <button class="btn_general" @click="confirm_change()"></button>
                        </div>
                    </footer>
                </div>
            </div>
        </div>

        <!---->
        <div class="page-box">
            <span class="warn_tip" style="display: block" v-model="tip">{{tip}}</span>
            <input class="btn_pages" style="margin-left: 0;" value="" @click="pre_page()">
            <span class="btn-pages" v-model="page,max_page">{{page}}/{{max_page}}</span>
            <input class="btn_pages" style="margin-right: 0;" value="" @click="next_page()">
        </div>

    </div>
</template>


<script type="text/javascript">
    export default{
        data(){
            return {
                show_create: false,
                show_change: false,

                tip: '',//
                isActive:[false,false,false,false], //
                add_if:true, // / 

                userData: [],
                user_name: '',
                account: '',
                pwd: '',
                confirm_pwd: '',
                back_index: '',
                back_username: '',
                back_account: '',
                get_url: '/ancient_books/get_user_list.action;this.get_user.page=1',//
                post_url: '/ancient_books/add_user.action',//
                modify_url: '/ancient_books/modify_user.action',//
                delete_url: '/ancient_books/delete_user_by_id.action',//

                page: 1,//
                max_page: 1,//

                get_user: {},//
                post_user:{},//
                delete_user:{},//
                modify_user:{}//
            }
        },
        created: function () {
            console.log("super_user created");
            this.getUsers(1);
        },
        methods: {
            // get 
            success_getUsers(response){
                    console.log("success get users ");
                    //
                    if( response.body.content.length === 0 ){
                        console.log("");
                    }else {
                        this.max_page = response.body.max_page;
                        for (let i = 0; i <= response.body.content.length-1; i++){
                            this.userData.push({
                                user_name: response.body.content[i].name,
                                account: response.body.content[i].account,
                                user_id: response.body.content[i].user_id
                            });
                        }
                    }
            },

            fail_getUsers(){
                console.log("fail get users!");
            },

            //
            getUsers(pages){
                this.get_user.key="page";
                this.get_user.value=pages;
                this.HttpGetForm(this.get_url,this.get_user,this.success_getUsers, this.fail_getUsers);
            },

            // post success
            success_postUsers(response){
                if(response.body.result===1)
                {
                    console.log("success create!");
                    this.userData.splice(0,this.userData.length);//
                    this.getUsers(1);  //get
                    /*this.userData.push({   //
                        user_name: this.user_name,
                        account: this.account,
                        pwd: this.pwd,
                        user_id: response.body.user_id
                    });*/
                    // this.info_total=this.userData.length;
                    //this.page=(this.info_total+ this.info_num-1)/this.info_num;

                }else if (response.body.result===0) {
                    console.log("fail create!");
                }
                this.user_name = '';
                this.account = '';   //
                this.pwd = '';
                this.confirm_pwd = '';
                this.show_create = false;
            },

            fail_postUsers(){
                console.log("error create!");
            },

            createUsers(){
                this.post_user.name=this.user_name;
                this.post_user.account=this.account;
                this.post_user.pwd=this.pwd;
                this.post_user.token=this.Token;
                this.BeforeHttp(this.post_user);
                this.post_user.token=this.Token;
                this.HttpPostForm(this.post_url,this.post_user,this.success_postUsers,this.fail_postUsers);
            },

            // 
            open_dialog(){
                this.show_create = true;
                this.user_name="";
                this.account="";
                this.pwd="";
                this.confirm_pwd="";
                this.tip="";
                this.isActive[0]=false;
                this.isActive[1]=false;
                this.isActive[2]=false;
                this.isActive[3]=false;
            },

            close_dialog() {
                this.show_create = false;
            },

            //
            checkIn(tag1,placeholder_text,juge_text,index){
                let x=document.getElementById(tag1).value;
                if(x!==""){
                    if(x.match(juge_text)){
                        this.tip="";
                        document.getElementById(tag1).placeholder =placeholder_text;
                        this.add_if=true;
                        this.isActive[0]=false;
                        this.isActive[1]=false;
                        this.isActive[2]=false;
                    }else{
                        this.tip=placeholder_text;
                        document.getElementById(tag1).placeholder = placeholder_text;
                        this.add_if=false;
                        this.isActive[index]=true;
                    }
                }else{
                    document.getElementById(tag1).placeholder =placeholder_text;
                    this.add_if=false;
                    this.isActive[0]=false;
                    this.isActive[1]=false;
                    this.isActive[2]=false;
                }
            },

            //
            check_pwd(tag1,tag2){
                let x=document.getElementById(tag1).value;
                let y=document.getElementById(tag2).value;
                if(y!==""){
                    if(y!==x){
                        this.tip="";
                        document.getElementById(tag2).placeholder = "";
                        this.add_if=false;
                        this.isActive[3]=true;
                    }else{
                        this.tip="";
                        document.getElementById(tag2).placeholder = "";
                        this.add_if=true;
                        this.isActive[3]=false;
                    }
                }else{
                    document.getElementById(tag2).placeholder = "";
                    this.add_if=false;
                    this.isActive[3]=false;
                }
            },

            //
            add_msg(){
                let x = document.getElementById("text1").value;
                let y = document.getElementById("text2").value;
                let z = document.getElementById("text3").value;
                let m = document.getElementById("text4").value;
                if (x === "" || y === "" || z === "" || m === "") {
                    this.tip = ""
                } else {
                   this.checkIn('text1','2-10','[\u4e00-\u9fa5a-zA-Z0-9]{2,10}$',0);
                    this.checkIn('text2','8-10','[a-zA-Z0-9]{8,10}$',1);
                    this.checkIn('text3','6-16','[a-zA-Z0-9_]{6,16}$',2);
                    this.check_pwd('text3','text4');
                    if (this.add_if===true) {
                        //push,this
                        this.createUsers();
                    }
                }
            },

            // success
            success_delete(response){
                if (response.body.result === 1) {
                    console.log("success delete");
                    this.userData.splice(0, this.userData.length);//splice
                    this.getUsers(this.page);//
                } else if (response.body.result === 0) {
                    console.log("fail delete");
                }
            },

            fail_delete(){
                console.log("error delete");
            },

            deleteUsers(index){
                this.delete_user.user_id=this.userData[index].user_id;
                this.delete_user.token=this.Token;
                this.BeforeHttp(this.delete_user);
                this.delete_user.token=this.Token;
                this.HttpPostForm(this.delete_url,this.delete_user,this.success_delete,this.fail_delete);
            },


            // 
            open_chaDialog(index){
                this.show_change=true;
                this.back_index = index;
                this.back_username =this.userData[index].user_name;
                this.back_account = this.userData[index].account;
                this.tip="";
                this.isActive[0]=false;
                this.isActive[2]=false;
                this.isActive[3]=false;

            },

            close_chaDialog(){
                this.show_change=false;
                this.back_username='';
                this.back_account='';
                this.pwd='';
                this.confirm_pwd = '';
                this.back_index='';
            },

            success_modify(response){
                if(response.body.result===1){
                    console.log("success modify!");
                    //this.userData.splice(this.back_index,1,{user_name:this.back_username,account:this.userData[this.back_index].account,pwd:this.pwd});
                    this.userData.splice(0,this.userData.length);//
                    this.getUsers(this.page);
                } else if(response.body.result===0){
                    console.log("fail modify!");
                }
                this.show_change= false;
                this.back_account='';
                this.back_index='';
                this.confirm_pwd='';
                this.pwd='';
                this.back_username='';
            },

            fail_modify(){
                console.log("error modify!");
            },

            //
            modifyUsers(){
                this.modify_user.name=this.back_username;
                this.modify_user.pwd=this.pwd;
                this.modify_user.user_id=this.userData[this.back_index].user_id;
                this.modify_user.token=this.Token;
                this.BeforeHttp(this.modify_user);
                this.modify_user.token=this.Token;
                this.HttpPostForm(this.modify_url,this.modify_user,this.success_modify,this.fail_modify);
            },

            //
            confirm_change(){
                let x=document.getElementById("chan2").value;
                let y=document.getElementById("chan3").value;
                let z=document.getElementById("chan4").value;
                if (x === "" || y === "" || z === "" ) {
                    this.tip = ""
                } else {
                    this.checkIn('chan2','2-10','[\u4e00-\u9fa5a-zA-Z0-9]{2,10}$',0);
                    this.checkIn('chan3','6-16','[a-zA-Z0-9_]{6,16}$',2);
                    this.check_pwd('chan3','chan4');
                    if (this.add_if===true) {
                        this.modifyUsers();
                    }
                }
            },

            //
            pre_page(){
              if(this.page===1){
                  this.tip="";
              }else if(this.page>1){
                  this.tip="";
                  this.page=this.page-1;
                  this.userData.splice(0,this.userData.length);//
                  this.getUsers(this.page);
                }
            },

            //
            next_page(){
                if(this.page===this.max_page){
                    this.tip="";
                }else if(this.page>=1 && this.page<=this.max_page){
                    this.tip="";
                    this.page=this.page+1;
                    this.userData.splice(0,this.userData.length); //
                    this.getUsers(this.page);
                }
            }
        }
    }

</script>



<style>
    /*div,*/
    #table_container{
        /*position: relative;*/
        padding-top: 60px;
        bottom: 0;
        left: 0;
        right: 0;
        margin:auto;
        height:10%;
        width: 52%;
    }
    /**/
    #table_all{
        background-color: #f7e1b5; /**/
        border-style: solid;      /**/
        border-color: sienna ;    /**/
        border-collapse: collapse; /**/
        vertical-align: middle;   /**/
        width:800px;
        height: 15px;/**/
        table-layout:fixed ;      /**/

    }

    /**/
    .table_border{
        padding: 5px;/**/
        border-style: solid;/**/
        border-color: sienna ;/**/
        text-align: center;/**/
        font-family: ;
        color: dimgrey;
        font-size: 20px;

    }

    /**/
    .btn_general{
        background-color:sienna ;/**/
        color: #F2F2F2;/**/
        width:70px;
        height: 28px;
        border-radius: 8px;
        border-color: transparent;
    }

    /**/
    #user_text{
        text-align: left;
    }

    .span_user{
        font-family: ;
        font-size: 20px;
        font-weight: bold;
        color: dimgrey;
    }

    #create_user{
        margin-top: 1%;
        margin-bottom: 1%;
    }

    .dialog {
        width: 480px;
        position: fixed;
        left: 50%;
        top: 6em;
        transform: translateX(-50%);
        z-index: 2000;
        visibility: hidden;
        backface-visibility: hidden;
        perspective: 1300px;
    }

    .dialog-active{
        visibility: visible;
    }

    .dialog-active .dialog-content {
        position: relative;
        opacity: 1;
        transform: rotateY(0);
    }

    .dialog-active ~ .dialog-overlay {
        opacity: 1;
        visibility: visible;
    }

    .dialog-content {
        border-radius: 2px;
        background: #fff;
        overflow: hidden;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
        transition: .5s ease-in-out;
        opacity: 0;
        transform-style: preserve-3d;
        transform: rotateY(-70deg);
    }

    .dialog-header {
        background: #fff;
        margin-top: 3%;
    }

    .dialog-title {
        color: dimgrey;
        font-family: ;
        margin-left: 35%;
        margin-right: 33%;
        font-size: 25px;
        text-align: center;
        font-weight: 200;
        line-height: 1em;

    }

    .dialog-body {
        padding: 2em;
    }

    .dialog-footer {
        padding: 3% 0;
        text-align: center;
        border-top: 1px solid rgba(0, 0, 0, 0.1);
    }

    .dialog-overlay {
        content: "";
        position: fixed;
        visibility: hidden;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1000;
        opacity: 0;
        background: rgba(0, 0, 0, 0.5);
        transition: all .6s;
    }

    .close {
        display: inline-block;
        width: 2rem;
        height: 2rem;
        position: absolute;
        top: .5rem;
        right: .5rem;
        transition: .8s ease all;
         -moz-transition: .8s ease all;
         -webkit-transition: .8s ease all;
        border: none;
        border-radius: 3px;
        color: #333;
        text-decoration: none;
        box-sizing: border-box;
         -webkit-box-sizing: border-box;
    }

    .close:hover {
        transition: .8s ease all;
         -moz-transition: .8s ease all;
         -webkit-transition: .8s ease all;
    }

    .close .iconfont {
        font-size: 2rem;
        color: black;
    }

    .rotate {
        cursor: pointer;
    }

    .rotate:hover {
        transition: transform 1.0s ease;
         -moz-transition: -moz-transform 1.0s ease;
         -webkit-transition: -webkit-transform 1.0s ease ;
    }

    .close .iconfont {
        font-size: 2rem;
        color: black;
    }

    .iconfont {
        font-size: 33px;
        font-style: normal;
        margin-top: -0.8%;
         -webkit-font-smoothing: antialiased;
         -webkit-text-stroke-width: 0.2px;
         -moz-osx-font-smoothing: grayscale;
    }
    .span_motal_1{
        font-family: ;
        font-size: 20px;
        font-weight: bold;
        color: dimgrey;

        text-align:left;
        margin-left: 10%;
    }
    .span_motal_2{
        font-family: ;
        font-size: 20px;
        font-weight: bold;
        color: dimgrey;
        left:34%;
        /*position:fixed;*/
        margin-left: 5%;
        margin-right: 5%;
    }
    .input_motal{
        width: 45%;
        right: 15%;
        position:fixed;
        text-align: center;
    }
    .warn_tip{
        color: #ff0000;
        font-size: 18px;
        text-align:center;
        font-family: ;
    }

    .warnBorder {
        border: 2px solid red;
    }

    .input_pwd{
        background: transparent;
        border-top: 0px;
        border-bottom: 0px;
        border-right: 0px;
        border-left: 0px;
        text-align: center;
    }
    .btn_pages{
        background-color:transparent ;/**/
        color: darkgrey;/**/
        width:70px;/**/
        height: 28px;
        border-radius: 8px;
        border-color: darkgrey;
        font-family: ;
        font-size: 15px;
        border: solid;
        /* position: absolute;*/
    }
    .page-box{
        text-align: center;
        padding: 1em 0;
        padding-top: 3em;
        padding-right: 0px;
        padding-bottom: 1em;
        padding-left: 0px;
        clear: both;
    }


</style>


